<script>
import { butter } from "@/plugins/buttercms"

export default {
    name: 'about',
    data() {
      return {
        about: '',
        education: '',
        resume: ''
      }
    },
    methods: {
      getAboutData() {
        butter.page.retrieve('*', 'homepage', {locale: 'en'})
          .then(res => {
              let obj = res.data.data.fields
              this.about = obj.about_me,
              this.education = obj.education,
              this.resume = obj.short_description
            })
      },
      animateSkills() {
        $('.skills-bar').each(function() {
            $(this).find('.bar').animate({
                width: $(this).attr('data-percent')
            }, 3000);
        });
      }
    },
    mounted() {
      this.getAboutData()
      this.animateSkills()
    }
}
</script>
